.van-info {
	position: absolute;
	top: 0;
	right: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	min-width: 16PX;
	padding: 0 3PX;
	color: #fff;
	font-weight: 500;
	font-size: 12PX;
