@charset "UTF-8";
html {
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: transparent;
  -webkit-user-select: none;
  -webkit-focus-ring-color: rgba(0, 0, 0, 0);
}
body {
  margin: 0;
  padding: 0;
  background-color: #fff;
  min-height: 100%;
  text-align: center;
  color: #404040;
  position: relative;
  font: 0.16rem/1.5;
  -webkit-text-size-adjust: 100% !important;
}
h1, h2, h3, h4, h5, h6, p, figure, form {
  margin: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
img {
  border: 0;
  vertical-align: middle;
  -ms-interpolation-mode: bicubic;
  -webkit-touch-callout: none;
}
a {
  text-decoration: none;
  color: #000;
  -webkit-touch-callout: none;
}
/* hn */
h1 {
  font-size: 0.213rem;
  line-height: 0.48rem;
}
h2 {
  font-size: 0.187rem;
  line-height: 0.4rem;
}
h3 {
  line-height: 0.32rem;
}
h3, h4, h5, h6 {
  font-size: 0.16rem;
}
/* ul ol dl */
ul, li, ol {
  margin: 0;
  padding: 0;
  list-style: none outside none;
}
ul.has-style li, ol li {
  margin-left: 0.333rem;
}
ul.has-style li {
  list-style: disc;
}
ol li {
  list-style: decimal;
}
ul.inline-style li {
  float: left;
  display: inline;
}
dl {
  margin-bottom: 0px;
}
dt {
  font-weight: bold;
}
dd {
  margin: 0 0 0 0px;
  padding: 0;
}
svg:not(:root) {
  overflow: hidden;
}
